.Layout_overlay__2Zu1q {
  position: fixed; /* Sit on top of the page content */
  display: none;
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  opacity: 0;
  transition: opacity 0.4s ease-out;
}

@media screen and (max-width: 1019px) {
  .Layout_list__Z5P1n,
  .Layout_signinList__3Yc0z {
    position: absolute;
    top: -100%;
    padding-left: 0;
    transition: top ease-out 0.5s, opacity ease-out 0.5s;
    z-index: 3;
  }

  .Layout_list__Z5P1n {
    flex-direction: column;
    justify-content: center;
    left: calc(50vw - 65px);
  }
  .Layout_signinList__3Yc0z {
    justify-content: space-around;
    left: calc(50vw - 55px);
  }

  .Layout_list__Z5P1n.Layout_open__3weOm {
    top: 40vh;
    opacity: 0.95;
  }
  .Layout_overlay__2Zu1q.Layout_open__3weOm {
    display: block;
    opacity: 0.95;
  }
  .Layout_signinList__3Yc0z.Layout_open__3weOm {
    top: 15vh;
  }
  .Layout_list__Z5P1n .Layout_item__WQ1Nx a {
    height: 60px;
    line-height: 60px;
  }
  .Layout_list__Z5P1n .Layout_item__WQ1Nx a.Layout_active__1PZGV::after {
    bottom: 8px;
    left: 14px;
  }
}
@media screen and (min-width: 1019px) {
