  height: 20px;
  padding: 5px;
}
.Modal_btnClose__1wZHs:hover {
  cursor: pointer;
}

.Modal_btnClose__1wZHs:before,
.Modal_btnClose__1wZHs:after {
  position: absolute;
  left: 15px;
  top: 0px;
  content: " ";
  height: 18px;
  width: 2px;
  background-color: #333;
}
.Modal_btnClose__1wZHs:before {
  transform: rotate(45deg);
}
.Modal_btnClose__1wZHs:after {
  transform: rotate(-45deg);
}

